exports.push([module.id, "\n:root{font-family:Marta;font-size:16px;line-height:1.4\n}\n.pop-up{z-index:105;position:fixed;top:0;left:0;width:100vw;height:100%;background-color:rgba(0,0,0,.8)\n}\n.pop-up,.pop-up>div{overflow-y:scroll\n}\n.pop-up-controls{position:fixed;display:flex;flex-wrap:wrap;max-width:50px;top:0;left:calc(25% - 100px);background-color:#fff\n}\n.pop-up-controls button{display:block;cursor:pointer;width:50px;height:50px;border:none;margin:10px 5px;background-color:#fff;background-repeat:no-repeat;background-position:50%;opacity:.5;transition:opacity .3s ease-in-out\n}\n.pop-up-controls button:hover{opacity:1\n}\n.pop-up-controls__publish{background-image:url(" + escape(require("../../img/success.svg")) + ")\n}\n.pop-up-controls__edit{background-image:url(" + escape(require("../../img/edit.svg")) + ")\n}\n.pop-up-controls__delete{background-image:url(" + escape(require("../../img/dustbin.svg")) + ")\n}\n.pop-up-window{width:50%;background-color:#fff;margin:0 auto;padding:40px\n}\n.pop-up-window>h2{color:#092e64\n}\n.pop-up-window>p{color:#092e64\n}\n.pop-up-window img{display:block;margin:0 auto;max-height:300px\n}", "", {"version":3,"sources":["D:/xampp/htdocs/tester-avalon/src/css/variables.css","D:/xampp/htdocs/tester-avalon/src/vue-comp/interface/src/vue-comp/interface/pop-up-test.vue"],"names":[],"mappings":";AAAA,MACI,kBAAqB,eACL,eACC;CAapB;AC0HD,QACA,YAAA,eACA,MACA,OACA,YACA,YACA,+BACA;CAIA;AAEA,oBAHA,iBAAA;CAKA;AAEA,iBACA,eAAA,aACA,eACA,eACA,MACA,uBACA,qBACA;CACA;AAEA,wBACA,cAAA,eACA,WACA,YACA,YACA,gBACA,sBACA,4BACA,wBACA,WACA,kCAGA;CACA;AAEA,8BACA,SAAA;CACA;AAEA,0BACA,8CAAA;CACA;AAEA,uBACA,8CAAA;CACA;AAEA,yBACA,8CAAA;CACA;AAGA,eACA,UAAA,sBACA,cACA,YACA;CACA;AAEA,kBAEA,aAAA;CACA;AAHA,iBAEA,aAAA;CACA;AAEA,mBACA,cAAA,cACA,gBACA;CACA","file":"pop-up-test.vue","sourcesContent":[":root {\r\n    font-family: 'Marta';\r\n    font-size: 16px;\r\n    line-height: 1.4;\r\n    --blue: #092E64;\r\n    --purple: #656695;\r\n    --darkpurple: #313149;\r\n    --marine: #3493A8;\r\n    --yellow: #EFDA7B;\r\n    --green: #9DBE87;\r\n    --red: #c74545;\r\n    --background: #e6e5e5;\r\n    --column: calc(100vw / 24);\r\n    --row: calc(100vh / 12);\r\n    --column-mobile: calc(100vw / 12);\r\n    --row-mobile: calc(100vh / 24);\r\n}\r\n","<template lang=\"html\">\r\n\r\n    <div class=\"pop-up\" @click=\"closeWindow\">\r\n        <div>\r\n                <div class=\"pop-up-window\" @click.stop>\r\n                    <div class=\"pop-up-controls\">\r\n                        <button class=\"pop-up-controls__publish\" v-if=\"!testStatus\" type=\"button\" name=\"button\" title=\"опубликовать\" @click=\"publish\"></button>\r\n                        <button class=\"pop-up-controls__edit\" v-if=\"!testStatus\" type=\"button\" name=\"button\" title=\"редактировать\" @click=\"edit\"></button>\r\n                        <button class=\"pop-up-controls__delete\" type=\"button\" name=\"button\" title=\"удалить\" @click=\"deleteTest\"></button>\r\n\r\n\r\n                    </div>\r\n                    <p v-if=\"testStatus == 1\" class=\"link\">{{testLink}}</p>\r\n                    <h2>{{testTitle}}</h2>\r\n                    <p>{{testDescription}}</p>\r\n                    <img :src=\"testImage\" alt=\"\">\r\n\r\n                    <question-item\r\n                    v-for=\"question in questions\"\r\n                    :key=\"question.question_id\"\r\n                    :qsttitle=\"question.question_description\"\r\n                    :vars=\"question.vars\"\r\n                    :answer=\"question.question_answer\"\r\n                    :qsttype=\"question.question_type_id\"\r\n                    >\r\n\r\n                </question-item>\r\n\r\n            </div>\r\n        </div>\r\n    </div>\r\n\r\n</template>\r\n\r\n<script>\r\n\r\nimport questionItem from './pop-up-question.vue';\r\nimport axios from './../../../node_modules/axios/dist/axios.js';\r\n\r\nexport default {\r\n\r\n    props: [\r\n        'testtite',\r\n        'description',\r\n        'imglink',\r\n        'anonym',\r\n        'time',\r\n        'status',\r\n        'testquestions',\r\n        'testid'\r\n    ],\r\n\r\n    components: {\r\n        'question-item': questionItem\r\n    },\r\n\r\n    data() {\r\n        return {\r\n            testTitle: this.testtite,\r\n            testDescription: this.description,\r\n            testImage: this.imglink ? this.imglink : 'img/default_test.svg',\r\n            testStatus: +this.status ? true : false,\r\n            testId: this.testid,\r\n            testLink: window.location.origin + '/exec?' + encodeURIComponent(btoa('test_id=' + (+this.testid))), //кодируем запрос в формат base64\r\n            testOptions: {\r\n                timeLimit: +this.time !== 0 ? true : false,\r\n                time: +this.time,\r\n                anonym: +this.status ? true : false\r\n            },\r\n            questions: this.testquestions\r\n        }\r\n    },\r\n\r\n    methods: {\r\n        closeWindow() {\r\n            // console.log(this.testId);\r\n            this.$emit('close-window');\r\n        },\r\n        edit() {\r\n            this.$emit('edit-test', this.testId);\r\n        },\r\n        //Удаляем тест\r\n        deleteTest() {\r\n\r\n            if(window.confirm('Вы точто хотите удалить тест?')) {\r\n                let query = this.testId;\r\n                axios.get(`php/deletetest.php?test_id=${query}`)\r\n                .then( (res) => {\r\n                    if(res.data.status) {\r\n                        let msg = 'Тест успешно удален из базы данных';\r\n                        this.$emit('delete-test', 3, msg);\r\n                        this.closeWindow();\r\n                    }\r\n                    else {\r\n                        let msg = 'Ошибка базы данных, попробуйте позже';\r\n                        this.$emit('delete-test', 3, msg);\r\n                        this.closeWindow();\r\n                    }\r\n                })\r\n                .catch( (err) => console.log(err));\r\n            }\r\n        },\r\n\r\n        //Публикуем тест\r\n        publish() {\r\n            let query = this.testId;\r\n            axios.get(`php/changeteststatus.php?test_id=${query}`)\r\n            .then( (res) => {\r\n                if(res.data.status) {\r\n                    let msg = 'Тест успешно опубликован';\r\n                    this.$emit('update-test', 3, msg);\r\n                    this.closeWindow();\r\n                }\r\n                else {\r\n                    let msg = 'Ошибка базы данных, попробуйте позже';\r\n                    this.$emit('update-test', 3, msg);\r\n                    this.closeWindow();\r\n                }\r\n            })\r\n            .catch( (err) => console.log(err));\r\n        }\r\n\r\n    },\r\n\r\n    created () {\r\n        document.body.style.overflow = 'hidden';\r\n    },\r\n\r\n    destroyed () {\r\n        document.body.style.overflow = 'auto';\r\n    }\r\n\r\n}\r\n</script>\r\n\r\n<style lang=\"css\">\r\n\r\n    @import './../../css/variables.css';\r\n\r\n    .pop-up {\r\n        z-index: 105;\r\n        position: fixed;\r\n        top: 0;\r\n        left: 0;\r\n        width: 100vw;\r\n        height: 100%;\r\n        background-color: rgba(0, 0, 0, 0.8);\r\n        /* padding-top: calc(var(--row) * 2);\r\n        padding-bottom: calc(var(--row) * 2); */\r\n        overflow-y: scroll;\r\n    }\r\n\r\n    .pop-up > div {\r\n        overflow-y: scroll;\r\n    }\r\n\r\n    .pop-up-controls {\r\n        position: fixed;\r\n        display: flex;\r\n        flex-wrap: wrap;\r\n        max-width: 50px;\r\n        top: 0;\r\n        left: calc(25% - 100px);\r\n        background-color: #fff;\r\n    }\r\n\r\n    .pop-up-controls button {\r\n        display: block;\r\n        cursor: pointer;\r\n        width: 50px;\r\n        height: 50px;\r\n        border: none;\r\n        margin: 10px 5px;\r\n        background-color: #fff;\r\n        background-repeat: no-repeat;\r\n        background-position: center;\r\n        opacity: .5;\r\n        -webkit-transition: opacity .3s ease-in-out;\r\n        -o-transition: opacity .3s ease-in-out;\r\n        transition: opacity .3s ease-in-out;\r\n    }\r\n\r\n    .pop-up-controls button:hover {\r\n        opacity: 1;\r\n    }\r\n\r\n    .pop-up-controls__publish {\r\n        background-image: url('./../../img/success.svg');\r\n    }\r\n\r\n    .pop-up-controls__edit {\r\n        background-image: url('./../../img/edit.svg');\r\n    }\r\n\r\n    .pop-up-controls__delete {\r\n        background-image: url('./../../img/dustbin.svg');\r\n    }\r\n\r\n\r\n    .pop-up-window {\r\n        width: 50%;\r\n        background-color: #fff;\r\n        margin: 0 auto;\r\n        padding: 40px;\r\n    }\r\n\r\n    .pop-up-window > h2,\r\n    .pop-up-window > p {\r\n        color: var(--blue);\r\n    }\r\n\r\n    .pop-up-window img {\r\n        display: block;\r\n        margin: 0 auto;\r\n        max-height: 300px;\r\n    }\r\n\r\n</style>\r\n"],"sourceRoot":""}]);

