      if(!v.result) wrong++;\r\n                });\r\n            });\r\n            return wrong;\r\n        }\r\n    }\r\n\r\n}\r\n</script>\r\n\r\n<style lang=\"css\">\r\n\r\n    @import './../../css/variables.css';\r\n\r\n    .stat-item {\r\n        display: flex;\r\n        width: 90%;\r\n        margin-bottom: 40px;\r\n        border: 1px solid var(--blue);\r\n        -webkit-box-shadow: 3px 3px 8px var(--purple);\r\n        box-shadow: 3px 3px 8px var(--purple);\r\n        cursor: pointer;\r\n        background-color: #fff;\r\n        font-weight: bold;\r\n        -webkit-transition: all .3s ease-in-out;\r\n        -o-transition: all .3s ease-in-out;\r\n        transition: all .3s ease-in-out;\r\n    }\r\n\r\n    .stat-item:hover .stat-item-title:before {\r\n        left: 0;\r\n    }\r\n\r\n    .stat-item:hover .stat-item-title h3 {\r\n        color: #fff;\r\n    }\r\n\r\n    .stat-item:hover .stat-item-title h3:after {\r\n        left: 0;\r\n    }\r\n\r\n    .stat-item:hover .stat-item-stat ul {\r\n        color: var(--purple);\r\n    }\r\n\r\n    .stat-item-title {\r\n        padding: 40px;\r\n        width: 50%;\r\n        position: relative;\r\n        overflow: hidden;\r\n    }\r\n\r\n    .stat-item-title h3 {\r\n        color: var(--blue);\r\n        z-index: 100;\r\n        position: relative;\r\n        -webkit-transition: all .3s ease-in-out;\r\n        -o-transition: all .3s ease-in-out;\r\n        transition: all .3s ease-in-out;\r\n    }\r\n\r\n    .stat-item-title h3:after {\r\n        display: block;\r\n        position: absolute;\r\n        content: '';\r\n        left: -100%;\r\n        bottom: -10px;\r\n        width: 40px;\r\n        height: 3px;\r\n        background-color: #fff;\r\n        -webkit-transition: all .3s ease-in-out .1s;\r\n        -o-transition: all .3s ease-in-out .1s;\r\n        transition: all .3s ease-in-out .1s;\r\n\r\n    }\r\n\r\n    .stat-item-title:before {\r\n        display: block;\r\n        position: absolute;\r\n        z-index: 99;\r\n        left: -100%;\r\n        top: 0px;\r\n        background-color: var(--purple);\r\n        content: '';\r\n        width: 100%;\r\n        height: 100%;\r\n        -webkit-transition: all .3s ease-in-out;\r\n        -o-transition: all .3s ease-in-out;\r\n        transition: all .3s ease-in-out;\r\n    }\r\n\r\n    .stat-item-stat {\r\n        padding: 40px;\r\n        width: 50%;\r\n    }\r\n\r\n    .stat-item-stat ul{\r\n        list-style: none;\r\n        display: flex;\r\n        justify-content: space-around;\r\n        flex-wrap: wrap;\r\n        color: var(--blue);\r\n    }\r\n\r\n    .stat-item-stat li {\r\n        display: block;\r\n        width: 30%;\r\n        text-align: center;\r\n    }\r\n\r\n    li[class^=\"stat-item-stat\"] {\r\n        font-size: 2rem;\r\n    }\r\n\r\n</style>\r\n"],"sourceRoot":""}]);

