CA,GAAA,UAAA,QAAA;CAAA;CACA","file":"pop-up-exec.vue","sourcesContent":[":root {\r\n    font-family: 'Marta';\r\n    font-size: 16px;\r\n    line-height: 1.4;\r\n    --blue: #092E64;\r\n    --purple: #656695;\r\n    --darkpurple: #313149;\r\n    --marine: #3493A8;\r\n    --yellow: #EFDA7B;\r\n    --green: #9DBE87;\r\n    --red: #c74545;\r\n    --background: #e6e5e5;\r\n    --column: calc(100vw / 24);\r\n    --row: calc(100vh / 12);\r\n    --column-mobile: calc(100vw / 12);\r\n    --row-mobile: calc(100vh / 24);\r\n}\r\n","<template lang=\"html\">\r\n\r\n\r\n    <div class=\"pop-up-exec\">\r\n\r\n        <div class=\"pop-up-exec-container\">\r\n            <div class=\"pop-up-info\">\r\n                <h2>Результаты вашейго теста</h2>\r\n                <p>Количество правильных ответов: {{right}}</p>\r\n                <p>Количество неправильных ответов: {{wrong}}</p>\r\n                <p>Время затраченое на прохождение: {{timeLeft}}</p>\r\n                <button type=\"button\" name=\"button\" @click=\"endTest\">Завершить тестирование</button>\r\n            </div>\r\n\r\n        </div>\r\n\r\n        <div class=\"background\">\r\n            <div></div>\r\n        </div>\r\n</div>\r\n\r\n\r\n</template>\r\n\r\n<script>\r\nexport default {\r\n\r\n    props: ['results', 'timestart'],\r\n\r\n    data() {\r\n        return {\r\n            res: this.results,\r\n            time: this.timestart\r\n        }\r\n    },\r\n\r\n    computed: {\r\n        right() {\r\n            let right = 0;\r\n            this.res.result.answers.forEach( (v,i,a) => {\r\n                if(v.result) right++;\r\n            });\r\n            return right;\r\n        },\r\n\r\n        wrong() {\r\n            let wrong = 0;\r\n            this.res.result.answers.forEach( (v,i,a) => {\r\n                if(!v.result) wrong++;\r\n            });\r\n            return wrong;\r\n        },\r\n\r\n        timeLeft() {\r\n            let end = new Date(+this.res.time_end);\r\n            let start = new Date(this.time)\r\n            return end.getMinutes() - start.getMinutes();\r\n        }\r\n\r\n    },\r\n\r\n    methods: {\r\n        endTest() {\r\n            localStorage.clear();\r\n            this.$router.replace('/tester');\r\n        }\r\n    },\r\n\r\n    mounted() {\r\n        console.log('mounted');\r\n        console.log(this.res);\r\n    }\r\n\r\n}\r\n</script>\r\n\r\n<style lang=\"css\">\r\n\r\n    @import './../../css/variables.css';\r\n\r\n    .pop-up-exec {\r\n        position: fixed;\r\n        left: calc(var(--column) * 6);\r\n        top: 0;\r\n        width: calc(var(--column) * 15);\r\n        min-height: 50vh;\r\n        background-color: #fff;\r\n        z-index: 200;\r\n    }\r\n\r\n    .pop-up-exec-container {\r\n        width: 50%;\r\n        min-height: 50%;\r\n        position: fixed;\r\n        top: 25%;\r\n        left: 25%;\r\n        background-color: #fff;\r\n        -webkit-box-shadow: 3px 3px 8px var(--purple);\r\n        box-shadow: 3px 3px 8px var(--purple);\r\n        z-index: 202;\r\n    }\r\n\r\n    .background {\r\n        width: 52%;\r\n        min-height: 52%;\r\n        position: fixed;\r\n        top: 24%;\r\n        left: 24%;\r\n        overflow: hidden;\r\n    }\r\n\r\n    .background div {\r\n        height: 100vh;\r\n        width: 100vw;\r\n        left: -50%;\r\n        top: -50%;\r\n        position: absolute;\r\n        background-image: linear-gradient(#656695, #956565);\r\n        -webkit-animation: bgMove 5s ease-in-out infinite;\r\n        animation: bgMove 5s ease-in-out infinite;\r\n    }\r\n\r\n    .pop-up-info {\r\n        padding: 40px;\r\n        text-align: center;\r\n        color: var(--blue);\r\n    }\r\n\r\n    .pop-up-info {\r\n        margin: 20px 0px;\r\n    }\r\n\r\n    .pop-up-info p {\r\n        font-size: 1.2rem;\r\n        font-weight: bold;\r\n        margin-bottom: 20px;\r\n    }\r\n\r\n    .pop-up-info button{\r\n        text-align: center;\r\n        padding: 5px;\r\n        background-color: #fff;\r\n        border: 3px solid var(--purple);\r\n        cursor: pointer;\r\n        font-family: 'marta', sans-serif;\r\n        font-weight: bold;\r\n    }\r\n\r\n    @-webkit-keyframes bgMove {\r\n        0% { left: -60%; top: -60%;}\r\n        25% { left: 0%; top: 0%; }\r\n        50% { left: -0%; top: -50%}\r\n        100% { left: -50%; top: -50%;}\r\n    }\r\n    @-o-keyframes bgMove {\r\n        0% { left: -60%; top: -60%;}\r\n        25% { left: 0%; top: 0%; }\r\n        50% { left: -0%; top: -50%}\r\n        100% { left: -50%; top: -50%;}\r\n    }\r\n    @-moz-keyframes bgMove {\r\n        0% { left: -60%; top: -60%;}\r\n        25% { left: 0%; top: 0%; }\r\n        50% { left: -0%; top: -50%}\r\n        100% { left: -50%; top: -50%;}\r\n    }\r\n    @keyframes bgMove {\r\n        0% { left: -60%; top: -60%;}\r\n        25% { left: 0%; top: 0%; }\r\n        50% { left: -0%; top: -50%}\r\n        100% { left: -50%; top: -50%;}\r\n    }\r\n\r\n\r\n</style>\r\n"],"sourceRoot":""}]);

