height: 1.4;\r\n    --blue: #092E64;\r\n    --purple: #656695;\r\n    --darkpurple: #313149;\r\n    --marine: #3493A8;\r\n    --yellow: #EFDA7B;\r\n    --green: #9DBE87;\r\n    --red: #c74545;\r\n    --background: #e6e5e5;\r\n    --column: calc(100vw / 24);\r\n    --row: calc(100vh / 12);\r\n    --column-mobile: calc(100vw / 12);\r\n    --row-mobile: calc(100vh / 24);\r\n}\r\n","<template lang=\"html\">\r\n\r\n    <div class=\"respondents-page\">\r\n\r\n        <side-menu></side-menu>\r\n        <section class=\"content\">\r\n\r\n            <div class=\"loading\" v-if=\"loading\">\r\n\r\n                <loading-indicator></loading-indicator>\r\n\r\n            </div>\r\n\r\n            <div class=\"respondents-list\">\r\n\r\n                <div\r\n                    class=\"resondents-test-item\"\r\n                    v-for=\"test in testsArr\"\r\n                    :key=\"test.test_id\"\r\n                >\r\n\r\n                    <a class=\"resondents-test-item__show\" @click.prevent=\"showListHandler\" href=\"\">список <img src=\"img/arrow-down.svg\" alt=\"\"></a>\r\n                    <h2>{{test.test_name}}</h2>\r\n                    <respondent-item\r\n                        v-for=\"singleTry in test.tries\"\r\n                        :key=\"singleTry.test_answer_id\"\r\n                        :result=\"singleTry\"\r\n                    >\r\n                    </respondent-item>\r\n\r\n\r\n                </div>\r\n\r\n            </div>\r\n\r\n\r\n        </section>\r\n\r\n    </div>\r\n\r\n</template>\r\n\r\n<script>\r\n\r\nimport sideMenu from './side-menu.vue';\r\nimport loadingIndicator from './interface/loading.vue';\r\nimport respondentItem from './interface/respondent-item.vue';\r\nimport axios from './../../node_modules/axios/dist/axios.js';\r\n\r\nexport default {\r\n\r\n    components: {\r\n        'side-menu': sideMenu,\r\n        'loading-indicator': loadingIndicator,\r\n        'respondent-item': respondentItem\r\n    },\r\n\r\n    data() {\r\n        return {\r\n            loading: false,\r\n            testsArr: null\r\n        }\r\n    },\r\n\r\n    methods: {\r\n        fetchData() {\r\n            this.loading = true;\r\n            axios.get('php/getstats.php')\r\n            .then( (res) => {\r\n                this.loading = false;\r\n                this.testsArr = res.data.tests;\r\n                console.log(this.testsArr);\r\n\r\n            })\r\n            .catch( (err) => {\r\n                this.loading = false;\r\n                console.log(err);\r\n            });\r\n        },\r\n\r\n        //Разворачиваем список респондентов\r\n        showListHandler(e) {\r\n            e.target.parentNode.classList.toggle('resondents-test-item__active');\r\n            e.target.classList.toggle('active');\r\n        }\r\n\r\n    },\r\n\r\n    created() {\r\n        this.fetchData();\r\n    }\r\n\r\n}\r\n</script>\r\n\r\n<style lang=\"css\">\r\n\r\n@import './../css/variables.css';\r\n\r\n    .content {\r\n        margin-left: calc(var(--column) * 2);\r\n        margin-right: calc(var(--column) * 2);\r\n        width: calc(var(--column) * 16);\r\n    }\r\n\r\n    .respondents-page {\r\n        width: 100%;\r\n        padding-top: calc(var(--row) * 2);\r\n        display: flex;\r\n        background-color: var(--background);\r\n    }\r\n\r\n    .respondents-list {\r\n        width: 100%;\r\n        display: flex;\r\n        flex-flow: column;\r\n        justify-content: center;\r\n    }\r\n\r\n    .resondents-test-item {\r\n        max-height: calc(var(--row) * 4);\r\n        overflow: hidden;\r\n        position: relative;\r\n        margin-bottom: 40px;\r\n        padding: 20px;\r\n        -webkit-box-shadow: 1px 1px 4px rgba(0,0,0,0.1);\r\n        box-shadow: 1px 1px 4px rgba(0,0,0,0.1);\r\n        cursor: pointer;\r\n        background-color: #fff;\r\n        -webkit-transition: all 1s ease-in-out;\r\n        -o-transition: all 1s ease-in-out;\r\n        transition: all 1s ease-in-out;\r\n    }\r\n\r\n    .resondents-test-item__active {\r\n        max-height: 10000px;\r\n        /* height: auto; */\r\n    }\r\n\r\n    .resondents-test-item h2 {\r\n        margin-bottom: 20px;\r\n        color: var(--blue);\r\n    }\r\n\r\n    .resondents-test-item:after {\r\n        display: block;\r\n        position: absolute;\r\n        content: '';\r\n        width: 100%;\r\n        height: 100px;\r\n        top: calc(var(--row) * 4 - 100px);\r\n        background-image: linear-gradient(to top, #fff, rgba(255,255,255,0.6));\r\n    }\r\n\r\n    .resondents-test-item__show {\r\n        color: var(--purple);\r\n        font-size: .9rem;\r\n        font-weight: bold;\r\n        position: absolute;\r\n        padding-left: 30px;\r\n        right: 20px;\r\n        top: 20px;\r\n    }\r\n\r\n    .resondents-test-item__show.active img{\r\n        transform: rotate(180deg);\r\n    }\r\n\r\n    .resondents-test-item__show img {\r\n        max-width: 20px;\r\n        position: absolute;\r\n        top: 2px;\r\n        left: 0;\r\n        transform: rotate(0deg);\r\n        -webkit-transition: all .5s ease-in-out;\r\n        -o-transition: all .5s ease-in-out;\r\n        transition: all .5s ease-in-out;\r\n    }\r\n\r\n\r\n</style>\r\n"],"sourceRoot":""}]);

